@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap');

*{
    margin:0;
    padding: 0;
    box-sizing: border-box;
    border: 0;
    outline: 0;
    list-style: none;
    text-decoration: none;
}


/*======VARIABLES THAT ARE GOING TO BE USE FREQUENLY BY THE DIFFERENT COMPONENTS======*/

:root{
    --color-bg:#1f1f38; /*Background color*/
    --color-bg-variant:#2c2c6c;/**/
    --color-primary:#4db5ff;/**/
    --color-primary-variant:rgbd(77,181,255,0.4);/**/
    --color-white:#fff;/**/
    --color-light:rgba(255,255,255,0.6);/**/
    --color-black:black;

    --container-width-lg:75%;/**/
    --container-width-md:86%;/**/
    --container-width-ms:90%;/**/
    --transition:all 400ms ease;
}

html{
    scroll-behavior: smooth;/*Permit to scroll down on the page*/
}

::-webkit-scrollbar{
    display: none; /*Remove the scroll bar*/
}

body{
    font-family: 'Poppins', sans-serif;
    background-color: var(--color-bg);
    color: var(--color-white);
    line-height: 1.7; /*Hauteur de la ligne*/
   /* background-image: url(../src/assets/texture.png);*/
}

/* ======================GENERAL STYLES ======================== */

.container{
    width: var(--container-width-lg);
}

h1,h2,h3,h4,h5{
    font-weight: 500;
}

h1{
    font-size: 2.5rem;
}

section{
    margin-top: 8rem;
    height: 100vh;
}
section >h2 , section h5>{
    text-align: center;
}

section >h2{
    color: var(--color-primary);margin-bottom: 3rem;
}

section >h5{
    color: var(--color-light);
}

.text-light{
    color: var(--color-light);
}
a{
    color: var(--color-primary);
    transition:var(--transition);
}

a:hover{
    color: var(--color-white);
}

.btn{
    width: max-content;
    display: inline-block;
    color: var(--color-primary);
    padding:0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid var(--color-primary);
    transition: var(--transition);
}
.btn:hover{
    background-color: var(--color-white);
    color: var(--color-bg);
    border-color: transparent;
}

.btn-primary{
    background: var(--color-primary);
    color: var(--color-bg);
}

img{
    display: block;
    width: 100%;
    object-fit: cover;
}


/*============================== MEDIA QUERIES (MEDIUM DEVICES LIKE TABLETS) ============================*/
@media screen  and (max-width:1024px) {
    .container{
        width: var(--container-width-md);
    }
    section{
        margin-top: 6rem;
    }
}

/*============================== MEDIA QUERIES (SMALL DEVICES LIKE PHONES) ============================*/
@media screen  and (max-width:1024px) {
    .container{
        width: var(--container-width-sm);
    }
    section>h2{
        margin-bottom: 2rem;
    }
}